*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

nav {
  width: 100%;
  height: 10vh;
  background-color: #000000;
  opacity: 0.8;
  position: sticky;
  top: 0;
}



.navlist {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 80%;
  margin: 0 auto;
  margin-left: 100px;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  background-color: aqua;
}

section {
  width: 100vw;
  height: 100vh;
}
